@import '../../styles/utils';

@keyframes test {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.page {
  @include flex-box(100vw, 100vh);
  position: relative;

  & * {
    z-index: z-index('contents');
  }
}

.column {
  flex-direction: column;
}
.row {
  flex-direction: row;
}
